.prediction-settings__wrapper{width:680px}.prediction-settings__model-exists-info{margin-bottom:20px;background:var(--color-neutral-background);color:var(--color-negative-content);padding:10px 20px;border:1px solid var(--color-neutral-border);border-radius:8px;display:flex;justify-content:center;align-items:center;gap:10px}.prediction-settings__title-block{margin:20px 0;margin-top:0}.prediction-settings__title{font-weight:500;color:var(--color-neutral-content)}.ml{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:16px;margin-top:32px;align-items:flex-start;grid-auto-rows:max-content}.ml__info{display:flex;align-items:center;justify-content:flex-end}.ml__status{color:var(--color-neutral-content);display:flex;align-items:center;margin-right:10px}.ml__summary{margin-bottom:16px}.ml__indicator{width:8px;height:8px;display:block;margin-right:8px;border-radius:100%;background-color:var(--indicator-color)}.ml__indicator_state_CO{--indicator-color: var(--color-positive-surface)}.ml__indicator_state_DI{--indicator-color: var(--color-warning-surface)}.ml__indicator_state_ER{--indicator-color: var(--color-negative-surface)}.ml__indicator_state_TR,.ml__indicator_state_PR{--indicator-color: var(--color-primary-surface);position:relative}.ml__indicator_state_TR::before,.ml__indicator_state_PR::before{top:0;left:0;content:"";width:100%;height:100%;opacity:.5;display:block;border-radius:100%;background-color:var(--indicator-color);animation:state-pulse 2s ease infinite}@keyframes state-pulse{0%{transform:scale(1)}50%{opacity:0;transform:scale(3)}100%{opacity:0;transform:scale(3)}}