.heidy-tip__content{padding:1rem;border:1px solid var(--color-neutral-border);font-size:14px;border-radius:8px;box-shadow:0 2px 6px 0 rgba(0,0,0,.1);box-sizing:border-box;background:var(--color-neutral-surface)}.heidy-tip__header{display:flex;width:100%;min-height:24px;margin-bottom:8px;flex-direction:row;justify-content:space-between}.heidy-tip__title{font-size:16px;font-weight:500;line-height:24px;letter-spacing:.15px;color:var(--color-neutral-content)}.heidy-tip__text{line-height:20px;letter-spacing:.25px;color:var(--color-neutral-content-subtler)}.heidy-tip__link{color:var(--color-primary-content);font-weight:500;display:block}.heidy-tip__link:hover{color:var(--color-primary-content-hover);text-decoration:underline}.heidy-tip__link::before{content:" "}.heidy-tip__dismiss{width:24px;height:24px;padding:0;border:none;display:flex;box-shadow:none;justify-content:center;align-items:center}.heidy-tip__dismiss svg{width:14px;height:14px}.heidy-tip__dismiss svg .spike-fill{fill:var(--color-neutral-surface);display:none}.heidy-tip__dismiss svg .spike-stroke{fill:var(--color-neutral-border);display:none}.heidy-tip__dismiss svg>path{stroke:var(--color-neutral-content-subtlest)}.heidy-tip__dismiss:hover{background:var(--color-primary-emphasis-subtle)}.heidy-tip__dismiss:hover svg path{stroke:var(--color-primary-surface-content)}.heidy-tip__heidi{margin-top:-12px;padding-left:16px;pointer-events:none;color:var(--color-neutral-border)}.heidy-tip__heidi svg path.spike-stroke{color:red}