.card{border-radius:5px;background-color:var(--color-neutral-background);border:1px solid var(--color-neutral-border)}.card__header{display:flex;height:48px;padding:0 15px;align-items:center;font-weight:500;font-size:16px;line-height:18px;justify-content:space-between;box-shadow:0 1px 0 0 rgba(0,0,0,.1)}.card__header-content{display:flex;align-items:center}.card__content{padding:15px}.card:not(:first-child){margin-top:24px}