.export-page__finish{width:135px}.export-page__recent{display:grid;grid-auto-flow:rows}.export-page__footer{margin:0 -40px -32px;padding:24px 32px;position:sticky;bottom:-40px;background-color:var(--color-neutral-surface-active);border-top:1px solid var(--color-neutral-border)}.formats{color:var(--color-neutral-content-subtler);font-size:16px;padding-bottom:var(--spacing-base)}.formats__list{margin:10px -7px;display:flex;flex-flow:row wrap;gap:var(--spacing-tight)}.formats a{color:var(--color-primary-content);text-decoration:underline}.formats__item{padding:10px 9px 10px 38px;border-radius:4px;position:relative;pointer-events:none;color:var(--color-neutral-content);transition:all 150ms ease-out;border:1px solid rgba(0,0,0,0);width:100%}.formats__item_active{cursor:pointer;pointer-events:all}.formats__item_active:hover:not(.formats__item_selected){background-color:var(--color-primary-emphasis-subtle)}.formats__item:not(.formats__item_active){opacity:.3}.formats__item::before,.formats__item::after{top:50%;left:10px;width:18px;height:18px;content:"";display:block;position:absolute;border-radius:100%;transform:translate(0, -50%)}.formats__item::before{box-shadow:0 0 0 .5px var(--color-neutral-content-subtler)}.formats__item_selected{pointer-events:none;background-color:var(--color-primary-emphasis);border:1px solid var(--color-primary-border-subtler)}.formats__item_selected::after{left:12px;width:14px;height:14px;background-color:var(--color-primary-content)}.formats__item_selected::before{box-shadow:0 0 0 .5px var(--color-neutral-content)}.formats__name{display:flex;font-weight:500;justify-content:space-between}.formats__tag{font-size:12px;padding:2px 5px;border-radius:2px;font-weight:400;background-color:var(--color-primary-background);color:var(--color-primary-content)}.formats__description{font-size:14px;opacity:.7;color:var(--color-neutral-content-subtle)}